<template>
  <header>
    <div>
      <h1 style="margin-top: -10px;color: #425049;font-size:30px; ">欢迎来到网上实验教学系统</h1>
    </div>
    <div class="opt-wrapper">
      <el-dropdown :hide-on-click="false">
        <div class="demo-basic--circle">
          <div class="block">
            <el-avatar :size="40" :src="avatar" :class="['avatar-info']">
            </el-avatar>
          </div>
        </div>
        <el-dropdown-menu slot="dropdown" style="margin-top:-30px;margin-left: -40px;">
          <el-dropdown-item>您好，{{$store.getters.username}}</el-dropdown-item>
          <el-dropdown-item icon="el-icon-s-home" @click.native="mypage()" >个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="logout()"><i class="el-icon-switch-button"></i>退出登录</el-dropdown-item>
          <el-dropdown-item @click.native="attendance()"><i class="el-icon-s-help"></i>我要签到</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      attendancenum:"",
      avatar: require('@/assets/image/touxiang.jpg')
    }
  },
  methods:{
    mypage() {
      this.$router.push({
        path: "/myinfo",
      });
    },
    logout(){
      this.$axios.post("logout");
      this.$router.push({
        path: "/login",
      });

    },
    attendance(){
      var _this = this;
      this.$axios
          .post("/addattendance", {
            userid: this.$store.state.userId,
            })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              this.attendancenum=successResponse.data.result.attendancenum
              this.$message.success("签到成功！当前签到次数："+this.attendancenum+"次");
              this.getdata();
            }
            else{
              this.$message.error("签到失败");
            }
          }).catch(failResponse => {
        
      })
    }
  }
}
</script>

<style scoped>
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}
/*设定头像图片样式*/
.avatar-info {
  margin-top: 10px;
  margin-right: 40px;
  cursor: pointer;
}
</style>



